<style lang="less">
    .check-box {
        width: 400px;
        text-align: center;
        padding: 40px 40px;
        border: 1px solid rgba(255, 255, 255, .2);
        margin: 20px auto;
        color: white;
        border-radius: 4px;
        box-shadow: 8px 8px 120px rgba(0, 0, 0, 0.45);
        background-color: rgba(76, 86, 125, 0.6);
        .avator {
            margin-bottom: 25px;
            >span {
                cursor: pointer;
                &:hover {
                    color: rgb(54, 79, 179);
                }
            }
        }
        .classmate {
            width: 300px;
            margin-bottom: 25px;
            > input {
                color: #b4b6bb;
                -moz-user-select:none !important;
                -webkit-user-select:none !important;
                user-select:none !important;    
            }
        }
        .examtype {
            width: 300px;
            margin-bottom: 25px;
        }
        .enter-btn {
            width: 300px;
        }
    }
    .answer_box {
        width: 80%;
        border: 1px solid rgba(255, 255, 255, .2);
        margin: 20px auto;
        color: white;
        box-shadow: 8px 8px 120px rgba(0, 0, 0, 0.45);
        background-color: rgba(76, 86, 125, 0.6);
        > div:not(.box_header) {
            padding: 25px 0;
        }
        > div {
            margin: 0 15px;
        }
        .box_header {
            display: flex;
            border-bottom: 1px solid rgba(255, 255, 255, .2);
            padding: 8px 0;

            .pro {
                flex: 8;
                padding: 0 50px;
                text-align: center;
                margin-top: 5px;
            }
            .total {
                flex: 2;
                text-align: center;
                line-height: 30px;
                color: white;
            }
            .time {
                flex: 2;
                text-align: center;
                line-height: 30px;
                color: white;
            }
        }
        .tab {
            border-bottom: 1px solid rgba(255, 255, 255, .2);
            span {
                font-size: 14px;
            }
            i {
                color: #97ABFF;
                margin-right: 5px;
            }
        }
        .answer_place {
            border-bottom: 1px solid rgba(255, 255, 255, .2);
        }
        .submit-box {
            padding-bottom: 20px !important; 
        }
    }
    .main-title {
        text-align: center;
        font-size: 24px;
        padding: 45px;
        letter-spacing: 16px;
    }
 
    @media screen and (max-width: 768px) {
        .el-radio,
        .el-radio__input,
        .el-checkbox__label {
            white-space:  normal !important;
            word-break: break-all;
        }
        .el-checkbox__input {
            vertical-align: top !important;
        }

        .check-box .classmate,
        .check-box .examtype,
        .enter-btn {
            width: 100% !important;
        }
        .el-message-box {
            width: 280px !important;
        }
        .el-message{
            min-width: 280px !important;
        }
        .main-title {
            display: none;
        }
        .answer_box_wrapper {
            height: 100%;
        }
        .check-box {
            width: 100%;
            height: 100%;
            padding-top: 40%;
            box-sizing: border-box;
            margin: 0;
        }
        .answer {
            width: 100%;
            height: 100%;
            overflow: hidden;
            .answer_box {
                width: 100%;
                height: 100%;
                margin: 0 auto;
            }
            .box_header {
                flex-direction: column;
            }
            .pro,.total,.time {
                 margin-bottom: 8px;
            }
             .submit-box {
                 display: flex;
                 margin-top: 20px;
                 button {
                     flex: 1;
                     margin: 0 10px !important;
                 }
             }
        }
        
    }

    // 选择框样式
    .el-radio,
    .el-checkbox {
        color: white !important;
        margin-left: 10px;
        margin-bottom: 15px;
        display: block !important;
    }

    .el-radio__label,
    .el-checkbox__label {
        width: 100% !important;
       
    }

    .el-radio__input.is-checked+.el-radio__label,
    .el-checkbox__input.is-checked+.el-checkbox__label {
        color: white !important;
    }

    // 上传文件处样式
     .upload {
        margin-top: 15px;
        .el-upload__tip {
            color: white;
        }
        
    }
    .el-upload-list__item-name {
        color: white !important;
    }
    .el-upload-list__item:hover {
        background-color: #6062666b !important;
        cursor: pointer;
    }
    .el-upload-list__item {
        outline: none;
        
    }
</style>

<template>
    <div class="answer">
        <div v-if="page == 'checkbox'" class="check-box center">
            <div class="avator">
                <span @click="$router.push('/score')">查看以前考试成绩</span>
            </div>
            <el-input
                class="classmate"
                placeholder="当前班级"
                v-model="input"
                readonly
            >
            </el-input>
            <br>
             <el-input
                class="classmate"
                placeholder="当前班级"
                v-model="options[0].major_name"
                readonly
            >
            </el-input>
            <br>
            <el-select class="examtype" v-model="value" placeholder="请选择考试科目">
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.subject_name"
                    :value="item.id"
                >
                </el-option>
            </el-select>
            <br>
            <el-button 
                class="enter-btn"
                type="primary"
                @click="startExam" 
                plain
            >开始考试</el-button>
           
            <!-- <el-select v-model="value" placeholder="请选择">
                <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select> -->
        </div>
        <div v-if="page == 'answerbox'" class="answer_box_wrapper">
            <h3 class="main-title">请同学认真考试哦</h3>
            <div class="answer_box">
                <div class="box_header">
                    <div class="pro">
                        <m-process :rate="rate"></m-process>
                    </div>
                    <span class="total">当前第{{cur}}题共{{questions.length}}题</span>
                    <span class="time">剩下时间: {{ formatTime }}</span>
                </div>
                <div class="tab">
                    <i class="iconfont icon-label_fill"></i>
                    <span>{{ typeName }}</span>
                </div>
                <div class="answer_place">
                    <tip-title :count="cur" :question="questions[cur - 1].title"></tip-title>
                    <div v-if="topicType == 2" class="answers">
                        <el-radio v-model="curTopic.answer" label="A">{{ curTopic.A }}</el-radio>
                        <el-radio v-model="curTopic.answer" label="B">{{ curTopic.B }}</el-radio>
                        <el-radio v-model="curTopic.answer" label="C">{{ curTopic.C }}</el-radio>
                        <el-radio v-model="curTopic.answer" label="D">{{ curTopic.D }}</el-radio>
                    </div>
                    <div v-if="topicType == 1" class="answers">
                        <el-radio v-model="curTopic.answer" label="1">对</el-radio>
                        <el-radio v-model="curTopic.answer" label="0">错</el-radio>
                    </div>
                    <div v-if="topicType == 3" class="answers">
                         <el-checkbox-group v-model="curTopic.answer">
                            <el-checkbox label="A">{{ curTopic.A }}</el-checkbox>
                            <el-checkbox label="B">{{ curTopic.B }}</el-checkbox>
                            <el-checkbox label="C">{{ curTopic.C }}</el-checkbox>
                            <el-checkbox label="D">{{ curTopic.D }}</el-checkbox>
                        </el-checkbox-group>
                        <!-- <tip data-type='3' type='checkbox' :check='questions[cur - 1].answer && questions[cur - 1].answer.includes("A")' value='A' @changeValue='changeValue' name="answer" :answer="questions[cur - 1].D"></tip>
                        <tip data-type='3' type='checkbox' :check='questions[cur - 1].answer && questions[cur - 1].answer.includes("B")' value='B' @changeValue='changeValue' name="answer" :answer="questions[cur - 1].D"></tip>
                        <tip data-type='3' type='checkbox' :check='questions[cur - 1].answer && questions[cur - 1].answer.includes("C")' value='C' @changeValue='changeValue' name="answer" :answer="questions[cur - 1].D"></tip>
                        <tip data-type='3' type='checkbox' :check='questions[cur - 1].answer && questions[cur - 1].answer.includes("D")' value='D' @changeValue='changeValue' name="answer" :answer="questions[cur - 1].D"></tip> -->
                    </div>
                    <div v-if="topicType == 4" class="answers">
                       <el-input
                            v-if="curTopic.pathinfo === ''"
                            type="textarea"
                            :rows="6"
                            placeholder="请输入内容"
                            resize='none'
                            data-type='4'
                            v-model="curTopic.answer">
                        </el-input>
                        <div class="upload-box" v-if="curTopic.pathinfo !== ''">
                            <div v-if="!curTopic.isUpload">
                                <p class='b15'>🔹 点击下面按钮进行下载考题</p>
                                <a class="my-btn b15" :href="downHref">点击下载考试题目</a>
                                <p class='b15'>🔹 题目做完后打包成zip点击下面按钮上传</p>
                                <el-upload
                                    class="upload"
                                    ref="upload"
                                    action="/api/upload"
                                    name="file"
                                    :limit="1"
                                    :data="{
                                        id_card: userinfo.id_card,
                                        subject_id: examingInfo.subject_id
                                    }"
                                    :on-success="handlePreview"
                                    :file-list="fileList"
                                    :auto-upload="true">
                                    <el-button slot="trigger" size="small" type="primary">选取文件进行上传</el-button>
                                </el-upload>
                            </div>
                            <div v-if="curTopic.isUpload">
                                🔹 当前操作题文件以上传,请去完成其他题 🔹 
                            </div>
                        </div>
                    </div>
                </div>
                <div class="submit-box">
                    <m-button :mstyle="buttonStyle" text="上一题" @callback="prev"></m-button>
                    <m-button :mstyle="buttonStyle" text="提交试卷" @callback="commit"></m-button>
                    <m-button :mstyle="buttonStyle" text="下一题" @callback="next"></m-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "../axios/index";
import {mapState, mapMutations} from "vuex"

import MProcess from "../components/global/m-process"
import tip from "../components/answer-box/tip"
import TipTitle from "../components/answer-box/tip-title"
import MButton from "../components/global/m-button"

export default {
   data() {
      return {
        buttonStyle: {fontSize:"12px", width: "80px", height: "30px", marginTop: "8px", marginRight: "20px"},
        type: [" ","判断题", "单选题", "多选题", "操作题"],
        page: "checkbox",
        options: [
            
        ],
        value: '',
        input: "",
        // 题目相关数据
        questions: [],
        cur: 1,
        finQuesCount: 0,
        curValue: null,
        // 时间相关处理
        targetTime: null,
        formatTime: null,
        // 切屏相关
        allowNum:3,
        // 信息提示(可以进行优化)
        message: null,
        // 记录是否有正在进行的考试
        examingInfo: {},
        // 上传文件相关
        fileList: []
      }
   },
   async created() {
        // 动态改变用户班级值
        this.input = this.userinfo.class_name
        this.options = this.userinfo.subject

        // 获取cookie值
        this.examingInfo = JSON.parse(this.getCookie('examingInfo'))
        if (!this.examingInfo) return
        if (this.examingInfo.user === this.userinfo.id_card) {
            this.info = this.$notify({
                title: '考试提醒',
                message: '你有正在进行的考试,点击继续考试',
                duration: 0,
                showClose: false,
                type: 'warning',
                onClick:() => {
                    // 如果点击后考试时间已过
                    if(Date.now() > this.localexpire) {
                        this.info.close()
                        return this.showMessage("当前考试已结束", 1)
                    }
 
                    this.questions = this.examingInfo.question
                    this.targetTime = this.examingInfo.time
                    this.subject_id = this.examingInfo.subject_id

                    // 继续计时和计算完成度
                    this.startTime()
                    this.calc()

                    // 防止切屏
                    this.stopShortCutKey()

                    // 切换
                    this.page = "answerbox"
                    this.info.close()
                }
            })
        }
        
   },
   // 优化点1: 在每次做完题就进行题目运算
   methods: {
    // 上传文件
    handlePreview(res) {
        if (res.code) {
            this.showMessage("上传成功", 1)
            this.curTopic.isUpload = true
            this.curTopic.answer = "上传成功"

            // 计算完成题目的总数
            this.calc()

            // 存储cookie信息
            this.examingInfo.question = this.questions
            this.setCookie('examingInfo', JSON.stringify(this.examingInfo), this.targetTime)
        } else {
            this.showMessage("上传失败,请重试", 2)
        }
    },
    // 开始计时
    startTime() {

        // 如果没有默认时间
        if (!this.targetTime) {
            let yuanTime = new Date()
            this.targetTime = yuanTime.setSeconds(yuanTime.getSeconds() + 5400)
        }
        

        this.timer = setInterval(() => {
            let curTime = this.targetTime - new Date() 
            this.formatTime = this.format(curTime)
            if (this.formatTime === "00:00") {
                this.showMessage('答题时间结束,自动交卷', 1)
                this.commitExam(2)
                clearInterval(this.timer)
            }
        })
    },
    // 重写消息框提示
    showMessage(message, type) {
        if (this.message) {
            this.message.close()
        }

        switch (type) {
            case 1:
                this.message = this.$message(message)
                break
            case 2:
                this.message = this.$message.error(message)
                break        
        }
    },
    // 防止弹出作弊
    stopShortCutKey() {  
        if (this.userinfo.id_card === "511922200101100516") return            
        if (this.userinfo.id_card === "510311200012282917") return            
        if (this.userinfo.id_card === "513821200105278856") return            
        let {allowNum} = this

        this.func = () => {
            if (this.curTopic && this.curTopic.type === 4) return
            allowNum--
            if(allowNum <= 0) {
               window.removeEventListener('blur', this.func)
               this.commitExam(1);
               return this.showMessage("自动交卷",2)
        　　}
            this.$alert('认真考试,否则将视为作弊', '提示', {
                confirmButtonText: '确定'
            })
        }

        window.addEventListener('blur', this.func) 
    },
    // 计算完成度百分比
    calc() {
        let count = 0
        this.questions.forEach(e => {
            if (e.answer && e.answer.length > 0) {
                count ++
            }
        })
        this.finQuesCount = count
    },
    next() {
        // 再判断切换
        if (this.cur >= this.questions.length) {
            this.showMessage('这已经是最后一题', 1)
            this.cur = this.questions.length
        } else {
            this.cur ++
        }     

        // 计算完成题目的总数
        this.calc()
        
        // 设置cookie
        this.examingInfo.question = this.questions
        this.setCookie('examingInfo', JSON.stringify(this.examingInfo), this.targetTime)
    },
    // 提交试卷
    async commit() {

        this.$confirm('确认提交试卷吗', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
        }).then(() => {
            if (this.finQuesCount !== this.questions.length) {
                return this.showMessage('必须完成所有题,才能交卷', 2)
            }
            
            // 发送请求
            this.commitExam()
            
        })
    },
    // 发送提交
    async commitExam(error = 0) {
        // error为1为作弊提交为2为自动提交
        let res = await axios.post("/api/commitQuestion",{
            questions: JSON.stringify(this.questions),
            subject_id: this.subject_id,
            error: error
        })

        if(res.code == 2) {
            this.showMessage('网络请求超时', 2)
        } else if (res.code == 1) {
            this.$router.push("/commit")
        } 

        // 清理cookie
        this.delCookie('examingInfo')
    },
    // 点击上一题
    prev() {
        // 再判断切换
        if (this.cur <= 1) {
            this.cur = 1
        } else {
            this.cur --
        }
        
        // 计算完成题目的总数
        this.calc()

        // 存储cookie信息
        this.examingInfo.question = this.questions
        this.setCookie('examingInfo', JSON.stringify(this.examingInfo), this.targetTime)
    },
    // 设置cookie
    setCookie(cname, cvalue, ctime) {
        let obj = {
            data: cvalue,
            time: Date.now(),
            expire: ctime
        };
        //localStorage 设置的值不能为对象,转为json字符串
        localStorage.setItem(cname, JSON.stringify(obj));

        // cookie解决超过4k无法存储
        // var d = new Date(ctime)
        // var expires = "expires="+ d
        // document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/"
    }, 
    // 获取cookie
    getCookie(name) 
    { 
        let val = localStorage.getItem(name);
        if (!val) {
            return val;
        }
        val = JSON.parse(val);
        if (Date.now() > val.expire) {
            localStorage.removeItem(name);
            return null;
        }
        // 加一个过期时间用于点击判断
        this.localexpire = val.expire
        return val.data;

        // cookie解决超过4k无法存储
        // var arr,reg=new RegExp("(^| )"+name+"=([^;]*)(;|$)")
        // return (arr=document.cookie.match(reg))?unescape(arr[2]):null
    },
    // 删除cookie
    delCookie(name)
    {
        localStorage.removeItem(name);

        // cookie解决超过4k无法存储
        // var exp = new Date();
        // exp.setTime(exp.getTime() - 1);
        // var cval=this.getCookie(name);
        // if(cval!=null)
        // document.cookie= name + "="+cval+";expires="+exp.toGMTString();
    },
    // 时间处理函数    
    format(time) {
        time = time / 1000
        let second = Math.floor(time % 60)
        second = second < 10 ? "0" + second : second
        let min = Math.floor(time / 60)
        min = min < 10 ? "0" + min : min
        return `${min}:${second}`
    },
    // 开始考试
    async startExam() {
        if (this.examingInfo && this.examingInfo.user) {
            return this.showMessage("当前浏览器有人正在进行考试请完成之后,在进行其他考试", 2)
        }
        if (!this.value) {  
            return this.showMessage("请选择专业后进行考试", 2)
        }
        let res = await axios.get("/api/getQuestion",{
            params: {
                subject_id: this.value,
                major_id: this.userinfo.major_id
            }
        })


        if(res.code == 2) {
            this.showMessage('网络请求超时', 2)
        } else if(res.code == 1) {
            // 检测当前科目是否考试完成
            if (res.exam_status) {
                return this.showMessage('当前科目已完成考试,请切换至其他科目', 2)
            }

            // 初始化题目
            let arr = []
            this.subject_id = res.subject_id
            res.judge.forEach(e => {
                e.type = 1
                e.title = e.judge_name
                arr.push(e)
            });
            res.single.forEach(e => {
                e.type = 2
                e.title = e.single_name
                arr.push(e)
            })
            res.selection.forEach(e => {
                e.title = e.selection_name
                e.type = 3
                e.answer = []
                arr.push(e)
            })
            
            res.operation.forEach(e => {
                e.type = 4
                e.title = e.operation_name
                arr.push(e)
            })

            // 检测当前考试是否有题
            if (arr.length === 0) {
                return this.showMessage('当前考试没有题目,请切换一个进行考试', 1)
            }
            
            // 倒计时功能
            this.startTime()
            this.showMessage(res.msg || '请在1小时内完成考试', 1)

            // 防止切屏
            this.stopShortCutKey()

            // 设置考试开始cookie存储
            this.examingInfo = {
                question: arr,
                user: this.userinfo.id_card,
                time: this.targetTime,
                subject_id: this.subject_id
            }
            this.setCookie('examingInfo', JSON.stringify(this.examingInfo), this.targetTime)
            // 切换盒子
            this.questions = arr;
            this.page = "answerbox"
        }
    }
   },
   components: {
       MProcess,
       TipTitle,
       tip,
       MButton,
   },
   destroyed() {
     // 清除定时器
     if (this.time) {
         clearInterval(this.timer)
         this.timer = null  
     }

     // 清除window事件
     if (this.func) {
         window.removeEventListener('blur', this.func)
     }
   },
   computed: {
       // 拿到模块划分后的数据
       ...mapState({
           userinfo: state => state.user.userinfo
       }),
       rate() {
           if (this.questions.length != 0) {
               return Math.round(this.finQuesCount/this.questions.length * 100) + "%"
           } else {
               return "0%";
           }
       },
       topicType() {
           return this.questions[this.cur - 1].type
       },
       curTopic() {
           return this.questions[this.cur - 1]
       },
       typeName() {
           let index = this.questions[this.cur - 1].type
           return this.type[index]
       },
       downHref() {
           return `/api/download?id=${this.curTopic.id}`
       }
   }
}
</script>
